import { Component, ViewContainerRef, Input, NgModule  }    from '@angular/core';
import { MdDialog, MdDialogConfig, MdDialogRef, MdInput }   from '@angular/material';
import { CoverService }                                     from '../body/cover/cover.service';
@Component({
    moduleId    : module.id,
    templateUrl : './deleteDialog.component.html',
    providers   : [CoverService]
})
export class deleteDialogComponent {
    private list_tvshows;
    constructor(public dialogRef: MdDialogRef<deleteDialogComponent>,
    private coverService:CoverService){
        this.coverService.getAll_TVShows().subscribe(
            list_tvshows    => {
                this.list_tvshows = list_tvshows;
            }
        );
    }

    close(){
        console.log('cancelar');
    }
    acept(){
        console.log('aceptar');
    }
}